<template>
  <div class="pos">
        <div class="top">收银台</div>
        <div class="container">
            <el-row >
                <el-col :span='7' id='order-list'>
                    <el-tabs v-model="activeName">

                        <el-tab-pane label="下单" name="first" class='order_style'>
                            <el-table :data="tableData" style="width: 100%;" height='250'>
                                <el-table-column fixed prop="name" label="商品名称" width="150"></el-table-column>
                                <el-table-column prop="price" label="金额" width="100"></el-table-column>
                                <el-table-column prop="num" label="数量" width="100"></el-table-column>
                                <el-table-column fixed="right" label="操作" width="150">
                                    <template slot-scope="scope">
                                        <el-button type="text" size="small" @click='reduce(scope.row)'>删除</el-button>
                                        <el-button type="text" size="small" @click='add(scope.row)'>增加</el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <div class="total">
                                数量：{{totalNum}} &nbsp;&nbsp;&nbsp;
                                总金额：{{totalMoney}}
                            </div>
                            <el-button type="warning" @click='putUp'>挂单</el-button>
                            <el-button type="danger" @click='delAll'>删除</el-button>
                            <el-button type="success" @click='pay'>结账</el-button>
                        </el-tab-pane>

                        <el-tab-pane label="挂单" name="second" class='order_style'>
                            <el-table :data="putupData" style="width: 100%;" height='250'>
                                <el-table-column fixed prop="name" label="商品名称" width="150"></el-table-column>
                                <el-table-column prop="price" label="金额" width="150"></el-table-column>
                                <el-table-column prop="num" label="数量" width="150"></el-table-column>
                            </el-table>
                        </el-tab-pane>
                        <el-tab-pane label="外卖" name="third" class='order_style'>外卖</el-tab-pane>
                    </el-tabs>
                    
                </el-col>
                <!--商品展示-->
                <el-col :span="17">
                    <el-tabs v-model="activeName2">
                        <el-tab-pane label="饮料" name="drink" class='order_style'>
                            <ul class='goodList'>
                                <li v-for='drink in drinkGoods' :key='drink.id' @click='add(drink)'>
                                    <span>{{drink.name}}</span>
                                    <span>￥{{drink.price}}.00</span>
                                    <span>{{drink.size}}</span>
                                </li>
                            </ul>
                        </el-tab-pane>

                        <el-tab-pane label="意面" name="noodle" class='order_style'>
                            <ul class='goodList'>
                                <li v-for='noodle in noodleGoods' :key='noodle.id' @click='add(noodle)'>
                                    <span>{{noodle.name}}</span>
                                    <span>￥{{noodle.price}}.00</span>
                                    <span>{{noodle.size}}</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                        <el-tab-pane label="甜点" name="cake" class='order_style'>
                            <ul class='goodList'>
                                <li v-for='cake in cakeGoods' :key='cake.id' @click='add(cake)'>
                                    <span>{{cake.name}}</span>
                                    <span>￥{{cake.price}}.00</span>
                                    <span>{{cake.size}}</span>
                                </li>
                            </ul>
                        </el-tab-pane>
                    </el-tabs>
                </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
export default {
  name: 'pos',
  data () {
    return {
        activeName: 'first',
        activeName2: 'drink',
        totalMoney:0,
        totalNum:0,
        tableData: [],
        putupData:[],
        noodleGoods:[{
                id:'21',
                name:'牛肉意面',
                price:38,
                size:'250g'
            },{
                id:'22',
                name:'番茄肉酱意面',
                price:48,
                size:'250g'
            }
        ],
        cakeGoods:[{
                id:'31',
                name:'黑森林',
                price:15,
                size:'1份'
            },{
                id:'32',
                name:'甜甜圈',
                price:12,
                size:'2份'
            },{
                id:'33',
                name:'奶油蛋糕',
                price:12,
                size:'1份'
            },{
                id:'34',
                name:'慕斯',
                price:12,
                size:'1份'
            }
        ],
        drinkGoods:[{
                id:'01',
                name:'百事可乐',
                price:12,
                size:'500ml'
            },{
                id:'02',
                name:'鲜橙汁',
                price:12,
                size:'500ml'
            }
        ]
    }
  },
   methods: {
      reduce:function (goods){
         
              if(goods.num !== 0){
                  let arr = this.tableData.filter(function (o){
                      return o.id == goods.id;
                  });
                  arr[0].num--;
              } else {
                   this.tableData = this.tableData.filter(o=>o.id != goods.id);
              }
              this.total();
          
      },
      add:function (goods){
           //1、遍历表格中的数据，看看商品是否已存在，存在则数量++，否则将数据写入数组
           let isExit=false;
          for(let i=0;i<this.tableData.length;i++){
              if(this.tableData[i].id == goods.id){
                  isExit=true;    //存在
              }
          }
          if(isExit){
                  let arr = this.tableData.filter(o => o.id == goods.id);
                  arr[0].num++;
                  console.log(arr);
              } else {
                  let newGoods = {id:goods.id,name:goods.name,price:goods.price,size:goods.size,num:1};
                  this.tableData.push(newGoods)
              }
              this.total();
      },
      //总数
      total:function (){
          this.totalMoney=0;
          this.totalNum=0;
          if(this.tableData){
            for(let i=0;i<this.tableData.length;i++){
                this.totalNum +=this.tableData[i].num;
                this.totalMoney += this.tableData[i].num*this.tableData[i].price;
            }
          }        
      },
    //   删除按钮
    delAll:function (){
        this.tableData=[];
        this.totalMoney=0;
        this.totalNum=0;
    },
    // // 结账
    pay:function (){
        if(this.totalNum){
            this.tableData=[];
            this.totalMoney=0;
            this.totalNum=0;
            this.$message({
                message: '结账成功，感谢你又为店里出了一份力!',
                type: 'success'
            });
        } else {
            this.$message.error('不能空结哦');
        }            
    },
    putUp:function (){
        if(this.totalNum){
            this.putupData=this.tableData;
            this.tableData=[];
            this.totalMoney=0;
            this.totalNum=0;
            this.$message({
                message: '挂单成功',
                type: 'success'
            });
            console.log(this.putupData)
        } else {
            this.$message.error('没有商品不能挂单哦');
        }
    }
    },

    mounted (){
        var orderHeight = document.body.clientHeight;
        var orderId = document.getElementById('order-list');
        orderId.style.height = orderHeight + 'px';
        orderId.style.borderRight = '1px solid #ccc';
        console.log(orderHeight)
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .pos{width:100%;}
   .top{height:60px;line-height:60px;box-shadow:0px 3px 1px #ccc;}
   /* 订单 */
   .aa{padding-left: 20px}
    #order-list{border-right:1px solid #ccc;}
    .goodList li{width:18%;float:left;margin:10px;background-color: #cc0000;color:#fff;padding:10px 0px;border-radius: 5px;}
    .goodList li span{display:block;height:30px;line-height: 30px;}

    .total{height:50px;line-height: 50px;border-bottom:1px solid #ccc;margin-bottom: 10px;}
</style>



