import Vue from 'vue'
import Router from 'vue-router'
import Pos from '@/components/pages/pos'
import member from '@/components/pages/member'
import set from '@/components/pages/set'
import procurement from '@/components/pages/procurement'
import repertory from '@/components/pages/repertory'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Pos',
      component: Pos
    },
    {
      path: '/member',
      name: 'member',
      component: member
    },
    {
      path: '/set',
      name: 'set',
      component: set
    },
    {
      path: '/procurement',
      name: 'procurement',
      component: procurement
    },
    {
      path: '/repertory',
      name: 'repertory',
      component: repertory
    }
  ]
})



